import { useState } from 'react';

const usePageAction = () => {
    // 
    const [pageAction, setPageAction] = useState(''); // 
    const [prePageAction, setPrePageAction] = useState(''); // 
    const [pageActionName, setPageActionName] = useState(''); // 
    const [searchPanel, setSearchPanel] = useState(true); // 
    const [searchTipPanel, setSearchTipPanel] = useState(true); // 
    const [listPanel, setListPanel] = useState(true); // 
    const [detailPanel, setDetailPanel] = useState(true); // 
    const [textDisabled, setTextDisabled] = useState(false); // 
    const [textHidden, setTextHidden] = useState(false); // 
    const [btnHiddenBtnSearch, setHiddenBtnSearch] = useState(); //   &
    const [btnHiddenBtnAdd, setHiddenBtnAdd] = useState(); //   &
    const [btnHiddenBtnModify, setHiddenBtnModify] = useState(); //   &
    const [btnHiddenBtnSave, setHiddenBtnSave] = useState(); //   &
    const [btnHiddenBtnRemove, setHiddenBtnRemove] = useState(); //   &
    const [btnHiddenBtnCancel, setHiddenBtnCancel] = useState(); //   &
    // //
    const handlePageAction = () => {
        switch (pageAction) {
            case 'List':
                setPageActionName('');
                // /
                setSearchPanel(false); // 
                setSearchTipPanel(true); // 
                setListPanel(false); // 
                setDetailPanel(true); // 
                // /
                setTextDisabled(true);
                //  (/)
                setTextHidden(true);
                //  (/)
                setHiddenBtnSearch(false); // 
                setHiddenBtnAdd(false); // 
                setHiddenBtnModify(true); // 
                setHiddenBtnSave(true); // 
                setHiddenBtnRemove(true); // 
                setHiddenBtnCancel(true); // 
                break;
            case 'Detail':
                setPageActionName('');
                // /
                setSearchPanel(false); // 
                setSearchTipPanel(true); // 
                setListPanel(false); // 
                setDetailPanel(false); // 
                // /
                setTextDisabled(true);
                //  (/)
                setTextHidden(false);
                //  (/)
                setHiddenBtnSearch(false); // 
                setHiddenBtnAdd(false); // 
                setHiddenBtnModify(false); // 
                setHiddenBtnSave(true); // 
                setHiddenBtnRemove(false); // 
                setHiddenBtnCancel(false); // 
                break;
            case 'Add':
                setPageActionName('');
                // /
                setSearchPanel(true); // 
                setSearchTipPanel(true); // 
                setListPanel(true); // 
                setDetailPanel(false); // 
                // /
                setTextDisabled(false);
                //  (/)
                setTextHidden(false);
                //  (/)
                setHiddenBtnSearch(true); // 
                setHiddenBtnAdd(true); // 
                setHiddenBtnModify(true); // 
                setHiddenBtnSave(false); // 
                setHiddenBtnRemove(true); // 
                setHiddenBtnCancel(false); // 
                break;
            case 'Modify':
                setPageActionName('');
                // /
                setSearchPanel(true); // 
                setSearchTipPanel(true); // 
                setListPanel(true); // 
                setDetailPanel(false); // 
                // /
                setTextDisabled(false);
                //  (/)
                setTextHidden(false);
                //  (/)
                setHiddenBtnSearch(true); // 
                setHiddenBtnAdd(true); // 
                setHiddenBtnModify(true); // 
                setHiddenBtnSave(false); // 
                setHiddenBtnRemove(true); // 
                setHiddenBtnCancel(false); // 
                break;
            case 'Cancel':
                // /
                setSearchPanel(false); // 
                setSearchTipPanel(false); // 
                setListPanel(true); // 
                setDetailPanel(true); // 
                // /
                setTextDisabled(true);
                //  (/)
                setTextHidden(true);
                //  (/)
                setHiddenBtnSearch(false); // 
                setHiddenBtnAdd(false); // 
                setHiddenBtnModify(true); // 
                setHiddenBtnSave(true); // 
                setHiddenBtnRemove(true); // 
                setHiddenBtnCancel(true); // 
                break;
            default:
                setPageActionName('');
                // /
                setSearchPanel(false); // 
                setSearchTipPanel(false); // 
                setListPanel(true); // 
                setDetailPanel(true); // 
                // /
                setTextDisabled(true);
                //  (/)
                setTextHidden(false);
                //  (/)
                setHiddenBtnSearch(false); // 
                setHiddenBtnAdd(false); // 
                setHiddenBtnModify(true); // 
                setHiddenBtnSave(true); // 
                setHiddenBtnRemove(true); // 
                setHiddenBtnCancel(true); // 
                break;
        }
    };

    return {
        setPrePageAction,
        prePageAction,
        setPageAction,
        pageAction,
        pageActionName,
        searchPanel,
        searchTipPanel,
        listPanel,
        detailPanel,
        textDisabled,
        textHidden,
        handlePageAction,
        btnHiddenBtnSearch,
        btnHiddenBtnAdd,
        btnHiddenBtnModify,
        btnHiddenBtnSave,
        btnHiddenBtnRemove,
        btnHiddenBtnCancel,
        setSearchPanel,
        setSearchTipPanel,
        setListPanel,
        setDetailPanel,
        setHiddenBtnCancel
    };
};

export default usePageAction;
