import React, { useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from "axios";
import useModal from './ModalCard';
import { Modal, Button } from 'react-bootstrap';
import { AuthContext } from './AuthContext';
import useWebState from './WebState';

const url = 'http://localhost:8088';

const ModalLogout = () => {
    // 
    const navigate = useNavigate(); // 
    const { showModal, handleShowModal, handleCloseModal } = useModal(); // Modal
    const { setIsLoginState } = useContext(AuthContext); // 
    const { webState } = useWebState();
    // submit Logout
    const handleLogout = async (event) => {
        event.preventDefault();
        Logout(event);
        handleCloseModal(); //  
        navigate('/'); //  
    };
    // Logout
    const Logout = async (event) => {
        event.preventDefault();
        const apiUrl = url + '/training/FrontendController/logout';
        const design = '/ReactDemo/Data/Layout/logout.json';
        const response = await axios.get((webState ? design : apiUrl)).then(rs => rs.data).catch(error => { console.log(error); });
        if (response.outputResultMessage.result === "S") {
            console.log('');
            setIsLoginState(false);
        } else {
            console.log('');
        }
    }

    return (
        <>
            {/* backdrop="modalonclickmodal" keyboard={ESCmodal} */}
            <Button variant="light" onClick={handleShowModal}>{'  '}</Button>
            <Modal show={showModal} onHide={handleCloseModal} animation={showModal} backdrop="static" keyboard={false} centered>
                <Modal.Header closeButton>
                    <Modal.Title>:</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {'?'}
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="primary" onClick={handleLogout}></Button>
                    <Button variant="light" onClick={handleCloseModal}></Button>
                </Modal.Footer>
            </Modal>
        </>
    );
};

export default ModalLogout;
