import React, { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { Table, Button } from 'react-bootstrap';

const PaymentDetail = () => {
    // 
    const navigate = useNavigate();
    const [tableList] = useState(['', '', '', '', '']); // table  
    const location = useLocation();
    const state = location.state;

    return (
        // 
        <div className='container'>
            <div className="d-flex justify-content-between align-items-center">
                <h2></h2>
                <Button variant="primary" className="ml-auto" onClick={() => navigate('/ReactDemo')}></Button>
            </div>
            <hr className='hr-primary' />
            {
                state !== null ? (
                    <>
                        <div>
                            <p>: {state.outputResultMessage.message}</p>
                            <p>: {state.name}</p>
                            <p>: {state.phone}</p>
                            <p>: {state.address}</p>
                            <p>: {state.inputPay}</p>
                            <p>: {(state.inputPay - state.outputShoppingCartGoodsInfo.totalAmount)}</p>
                        </div>
                        <div>
                            {
                                <Table className='text-center' striped bordered hover responsive>
                                    <thead>
                                        <tr>
                                            {
                                                tableList.map((item, index) => (
                                                    <th key={index}>{item}</th>
                                                ))
                                            }
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            state.outputShoppingCartGoodsInfo.shoppingCartGoods.length === 0 ? (
                                                <tr>
                                                    <td colSpan={tableList.length}>!</td>
                                                </tr>
                                            ) : (
                                                state.outputShoppingCartGoodsInfo.shoppingCartGoods.map((shoppingCartGoods, index) => (
                                                    <tr key={index}>
                                                        <td>{index + 1}</td>
                                                        <td>{shoppingCartGoods.goodsName}</td>
                                                        <td><div className='tr-imgs-size'><img className='tr-imgs-goods' src={'/ReactDemo/assets/DrinksImage/' + shoppingCartGoods.goodsImageName} alt='' /></div></td>
                                                        <td>{shoppingCartGoods.goodsPrice}</td>
                                                        <td>{shoppingCartGoods.buyQuantity === 0 ? "" : shoppingCartGoods.buyQuantity}</td>
                                                    </tr>
                                                ))
                                            )
                                        }
                                    </tbody>
                                </Table>
                            }
                        </div>
                    </>
                ) : (<div>.....</div>)
            }
        </div>
        // 
    );
}

export default PaymentDetail;
