import React from 'react';
import useModal from './ModalCard';
import { Modal, Button } from 'react-bootstrap';

const ButtonRemove = ({ onConfirm }) => {
    const { showModal, handleShowModal, handleCloseModal } = useModal();

    // handleSaveClick event:result: true & false
    const handleSaveClick = (event, result) => {
        event.preventDefault();
        onConfirm(result); //  handleSaveClick  :true \ :false
        handleCloseModal(event);
    };

    return (
        <>
            <>
                <Button variant="danger" type="submit" onClick={handleShowModal}></Button>{' '}
            </>
            <>
                <Modal show={showModal} onHide={handleCloseModal} animation={false} centered>
                    <Modal.Header closeButton>
                        <Modal.Title>:</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>?</Modal.Body>
                    <Modal.Footer>
                        <Button variant="primary" onClick={(event) => handleSaveClick(event, true)}> </Button>{' '}
                        <Button variant="secondary" onClick={(event) => handleSaveClick(event, false)}></Button>{' '}
                    </Modal.Footer>
                </Modal>
            </>
        </>
    );
};

export default ButtonRemove;