import { useState } from 'react';

const useModal = () => {
    const [showModal, setShowModal] = useState(false); //  false  &true 

    //  modal
    const handleShowModal = (event) => {
        event.preventDefault();
        setShowModal(true);
    };
    //  modal
    const handleCloseModal = () => {
        setShowModal(false);
    };

    return {
        showModal,
        setShowModal,
        handleShowModal,
        handleCloseModal
    };
};

export default useModal;
